import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import { Card, CardContent, CardMedia, Typography, CardActions, IconButton, SvgIcon, Chip, CardHeader} from '@material-ui/core'
import LinkIcon from '@material-ui/icons/Link'
import BuildIcon from '@material-ui/icons/Code'
import ReactPlayer from 'react-player'

/**
 * Preview Card
 * 
 * Card for showcasing the basic information of a project.
 */

const styles = theme => ({
    card:{
    },
    preview:{
    },
    chip:{
        marginRight: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
    },
})


class PreviewCard extends React.Component {
    

    static defaultProps = {
        data: {
            title:"No title",
            detail:"No description",
            video:"",
            link:"",
            github:"",
            vimeo:"",
            genre:[],
            brief:"No description",
            build:"",
            image:"",
        }
    }

    state = { expanded: false, open: false };

    handleExpandClick = () => {
        this.setState(state => ({ expanded: !state.expanded }));
    }

    handleOpenVideoModal = () =>{
        this.setState({open: true})
    }

    handleCloseVideoModal = () => {
        this.setState({open: false})
    }

    render() {
        const { classes, data }  = this.props;
        const { brief, detail, title, video, link, github, genre, build, image, vimeo} = data;

        return(
            <div>
                <Card className={classes.card} elevation={0}>
                    <CardHeader title={title} subheader={brief}/>
          
                    { (video || vimeo) ?

                        <CardMedia
                            title={title}
                            src={video || vimeo}
                            component={()=> (
                                <div style={{position:"relative", paddingTop:"56.25%"}}>
                                    <ReactPlayer
                                        style={{position:"absolute", top:0, left:0, bottom: 0, right: 0}}
                                        url={video || vimeo}
                                        width="100%"
                                        height="100%"
                                        controls
                                    />
                                </div>
                            )}
                        /> 
                    :
                    <CardMedia
                        className={classes.preview}
                        image={image}
                        title={title}
                        component='img'
                    />
                    }
                    <CardContent>
                        {detail &&
                            detail.split("\n").map( (line,index) => (
                                <Typography key={title.concat(index)} paragraph gutterBottom> {line} </Typography>
                        ))}

                        {
                            genre.map(tag => (
                                <Chip key={title+tag} label={tag} className={classes.chip}/>
                            ))
                        }
                    </CardContent>

                    <CardActions className={classes.actions}  justify="flex-end">
                        { link && 
                        <IconButton href={link} target="_blanks" aria-label="link">
                            <LinkIcon/>
                        </IconButton>
                        }

                        { github &&
                        <IconButton href={github} target="_blanks" aria-label="github link">
                            <SvgIcon>
                                <svg aria-labelledby="simpleicons-github-icon" role="img" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><title id="simpleicons-github-icon">GitHub icon</title><path d="M12 .297c-6.63 0-12 5.373-12 12 0 5.303 3.438 9.8 8.205 11.385.6.113.82-.258.82-.577 0-.285-.01-1.04-.015-2.04-3.338.724-4.042-1.61-4.042-1.61C4.422 18.07 3.633 17.7 3.633 17.7c-1.087-.744.084-.729.084-.729 1.205.084 1.838 1.236 1.838 1.236 1.07 1.835 2.809 1.305 3.495.998.108-.776.417-1.305.76-1.605-2.665-.3-5.466-1.332-5.466-5.93 0-1.31.465-2.38 1.235-3.22-.135-.303-.54-1.523.105-3.176 0 0 1.005-.322 3.3 1.23.96-.267 1.98-.399 3-.405 1.02.006 2.04.138 3 .405 2.28-1.552 3.285-1.23 3.285-1.23.645 1.653.24 2.873.12 3.176.765.84 1.23 1.91 1.23 3.22 0 4.61-2.805 5.625-5.475 5.92.42.36.81 1.096.81 2.22 0 1.606-.015 2.896-.015 3.286 0 .315.21.69.825.57C20.565 22.092 24 17.592 24 12.297c0-6.627-5.373-12-12-12"/></svg>
                            </SvgIcon>
                        </IconButton>
                        }

                        { build &&
                        <IconButton href={build} aria-label="build link" target="_blanks">
                            <BuildIcon/>
                        </IconButton>
                        }
                    </CardActions>
                </Card>
            </div>
        )
    }
}

export default withStyles(styles)(PreviewCard);


